#page {
    position: relative;
}

#primary {
    opacity: 0;
    transition-duration: 2s;

    &.loaded {
        opacity: 1;
    }
}

#section-contact {
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 60%;

    @include breakpoint(mobileonly) {
        background-size: cover;
    }


    .contact_form {
        @include breakpoint(mobileonly) {
            padding: 15px 30px;
        }
    }

    .contact_title {
        @include breakpoint(mobileonly) {
            padding-bottom: 30px !important;

            &:after {
                bottom: 15px !important;
            }
        }
    }

    .section_title {
        @include breakpoint(mobileonly) {
            display: block !important;
            left: 37%;
            height: auto;

            h2 {
                margin-top: 10px;
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
                text-align: center;
            }
        }
    }

    .contact_social {
        @include breakpoint(mobileonly) {
            .row {
                height: auto;
            }

            ul {
                margin-left: 0 !important;

                li {
                    padding: 10px !important;
                    height: 30px;
                    margin-bottom: 10px;

                    @include breakpoint(mobileonly) {
                        margin: 0;
                    }


                    .social-icon {
                        font-size: 18px !important;
                    }

                    .social-text {
                        @include breakpoint(mobileonly) {
                            display: none;
                        }
                    }
                }

                .social-location {
                    position: relative;
                    flex-grow: 6;

                    .location-text {
                        font-size: 0.8em;
                    }
                }
            }
        }
    }

    input {
        @include breakpoint(mobileonly) {
            margin-bottom: 5px;
        }
    }

    textarea {
        @include breakpoint(mobileonly) {
            height: 158px;
        }
    }

    label {
        @include breakpoint(mobileonly) {
            font-size: 9px;
        }
    }

    .fp-tableCell {
        display: block !important;
        height: inherit !important;
    }

    .row {
        height: 100%;
    }

    ul.row {
        height: auto;
    }

    .contact_social {
        background-color: rgba($color: $color_form, $alpha: 0.8);
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;

        li {
            list-style: none;
            color: #fff;

            .social-icon {
                font-size: 32px;
            }

            a {
                color: #fff !important;
            }
        }
    }

    .contact_form {
        background-color: $color_form;
        padding: 50px;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
    }

    .contact_title {
        position: relative;
        color: #fff !important;
        padding-bottom: 50px;

        &:after {
            display: block;
            position: absolute;
            bottom: 25px;
            left: 0;
            left: 33%;
            content: '';
            height: 2px;
            width: 33%;
            background-color: #fff;
        }
    }

    /*
      .empty-space {
          @include breakpoint(mobileonly) {
             height: 50px;
          }
      } */
    div.wpcf7-mail-sent-ok {
        border: 2px solid #fff !important;
        color: #fff;
    }
}

#section-home {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    @include breakpoint(mobileonly) {
        h2 {
            font-size: 18px !important;
        }
    }


    @-webkit-keyframes scroll {
        0% {
            opacity: 1;
            -webkit-transform: translateY(0);
            transform: translateY(0);
        }

        100% {
            opacity: 0;
            -webkit-transform: translateY(20px);
            transform: translateY(20px);
        }
    }

    @keyframes scroll {
        0% {
            opacity: 1;
            -webkit-transform: translateY(0);
            -ms-transform: translateY(0);
            transform: translateY(0);
        }

        100% {
            opacity: 0;
            -webkit-transform: translateY(20px);
            -ms-transform: translateY(20px);
            transform: translateY(20px);
        }
    }

    .jumbotron {
        background-color: transparent;
    }

    h1 {
        font-size: 5vw !important;
    }

    h2 {
        font-size: 3vw !important;
    }

    .mouse {
        display: block;
        margin: 0 auto;
        width: 36px;
        height: 56px;
        border-radius: 23px;
        border: 2px solid $primary;

        span {
            display: block;
            margin: 6px auto;
            width: 3px;
            height: 3px;
            border-radius: 4px;
            background: $primary;
            border: 1px solid transparent;
            -webkit-animation-duration: 1s;
            animation-duration: 1s;
            -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
            -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
            -webkit-animation-name: scroll;
            animation-name: scroll;
        }
    }
}

#section-services {
    $heigh-top: 35%;

    .fp-tableCell {
        display: block;
    }

    .top {
        position: relative;
        height: $heigh-top;
        overflow: hidden;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;

        .title {
            height: 85%;
            flex-direction: column;
            text-align: center;
            padding: 30px;

            h2 {
                color: #fff !important;
                margin: 0;
            }
        }

        .service-tabs {
            position: absolute;
            bottom: 0;
            background-color: rgba($color: #006f78, $alpha: 0.54);
            width: 100%;
            margin: 0 !important;

            @include breakpoint(mobileonly) {
                .nav-item {
                    padding: 0.2rem;

                    .nav-link {
                        padding: 0.3rem 0.5rem;
                        font-size: 1.2rem;
                    }
                }
            }


            .nav-item {
                padding: 1rem;
            }

            .nav-link {
                color: #fff;
                font-size: 24px;
                border: none;
                padding: 0 1rem 0.5rem;
                opacity: 0.5;
                transition-duration: 1s;

                &.active {
                    position: relative;
                    background-color: transparent !important;
                    opacity: 1;
                }

                &:hover {
                    border: none;
                }

                &:hover:not(.active) {
                    opacity: 1;
                }
            }
        }
    }

    .service-content {
        height: calc(100% - #{$heigh-top});

        .content {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            height: 100%;

            // background-color: #fff;
            // background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.03) 100%);
            .content_text {
                padding: 15px;
                width: 100%;
            }

            .content_img {
                position: relative;
                width: 100%;

                // height: 300px;
                overflow: hidden;

                svg {
                    font-size: 150px;
                    width: 100%;
                    color: #ccc;
                }

                /*         img {
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 100%;
                  display: block;
                  transition: all .5s;
                }
                &:hover img{
                  transform: scale(1.1);
                } */
            }
        }

        .content:nth-child(even) {
            // flex-direction: column-reverse;
       
        }
    }
}

#section-portfolio {
    position: relative;

    .fp-tableCell {
        @include breakpoint(mobileonly) {
            & {
                display: block;
            }
        }
    }

    .project-content {
        height: calc(100vh - 33px);
        overflow: hidden;
        padding-right: 30px;
        margin: 0;

        @include breakpoint(mobileonly) {
            & {
                padding-right: 0;
            }
        }
    }

    .tab-content {
        position: relative;
    }

    /*----------  Carrousel  ----------*/
    .carousel {
        .carousel-indicators {
            position: absolute;
            top: 5%;
            left: 97%;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            margin: 0;
            height: 75%;
            width: 20px;

            li {
                width: 15px;
                margin-bottom: 5px;
            }
        }

        .carousel-inner {
            @include ratio(16, 9);

            .carousel-item {
                height: 100%;

                img {
                    height: 100%;
                    width: 100%;
                    -o-object-fit: cover;
                    object-fit: cover;
                }
            }
        }
    }

    .project-desc {
        padding: 20px;
    }

    /*----------  Slide  ----------*/
    .slide {
        padding: 0 30px;

        @include breakpoint(mobileonly) {
            & {
                padding: 0 10px;
            }
        }
    }

    .fp-controlArrow {
        display: none;

        /*         top: inherit;
