        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }

    .fp-slidesNav.bottom {
        @include breakpoint(mobileonly) {
            & {
                bottom: 5px;
            }
        }
    }

    #fp-nav.right {
