#section-about {
    .btn-primary {
        background-color: #e77f67 !important;
        border-color: #e77f67 !important;
        color: #fff !important;
    }

    .fp-tableCell {
        display: block;
    }

    .about-bio {
        height: 100vh;

        @include breakpoint(mobileonly) {
            padding: 0 45px;
            align-items: inherit !important;
            height: inherit;
        }
    }

    .about-img {
        @include ratio(1, 1);
        @include breakpoint(mobileonly) {
            display: none;
        }


        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }

        &:after {
            position: absolute;
            bottom: 0;
            left: 0;
            content: '';
            height: 45px;
            width: 100%;
            background-color: rgba($color: #e77f67, $alpha: 0.54);
        }
    }

    .about-skills {
        padding: 30px 15px;
        height: 100%;
        background-color: #e77f67;

        h2 {
            @include heading-3;
        }
    }
}

.section-title {
    @include breakpoint(mobileonly) {
        -webkit-transform: inherit;
        transform: inherit;
        margin: 5px; // font-size: 18px;
        &:hover {
            cursor: text;
        }
    }


    &:hover {
        cursor: vertical-text;
    }
}

// animated
.right {
    opacity: 0;
    transition-duration: 2s;
}

.right.animated {
    right: 0;
    opacity: 1;
}
